<template>
  <view class="container">
    <!-- ÂïÜÂìÅÂ§ßÂõæ -->
    <view class="goods-image-container">
      <image :src="goods.cardGraph" mode="aspectFill" class="goods-image" alt="ÂïÜÂìÅÂõæÁâá"></image>
    </view>

    <!-- ÂïÜÂìÅ‰ø°ÊÅØ -->
    <view class="goods-info">
      <view class="goods-title-container">
        <text class="goods-title">{{ goods.name }}</text>
        <text class="goods-rare">{{ goods.rarity }}</text>
      </view>
      <text class="goods-code">ÁºñÂè∑: {{ goods.cardNumber }}</text>
      <!-- ÂçñÂÆ∂Âíå‰ª∑Ê†º -->
      <view class="seller-price-container">
        <text class="seller-name">ÂçñÂÆ∂: {{ goods.sellerId }}</text>
        <text class="goods-price">¬•{{ goods.price || 'ÊöÇÊó†‰ª∑Ê†º' }}</text>
      </view>
    </view>

    <!-- Êìç‰ΩúÊåâÈíÆ -->
    <view class="action-buttons">
      <button 
        class="action-button collect-btn" 
        :class="{ 'collected': goods.isCollected }"
        @click="toggleCollect"
      >
        <text class="action-icon">{{ goods.isCollected ? '‚≠ê' : '‚òÜ' }}</text>
        <text class="action-text">{{ goods.isCollected ? 'Â∑≤Êî∂Ëóè' : 'Êî∂Ëóè' }}</text>
      </button>
      <button class="action-button report-btn" @click="Buy">
        <text class="action-icon">üõí</text>
        <text class="action-text">Ë¥≠‰π∞</text>
      </button>
    </view>
<!--ÂèëÊ∂àÊÅØÁªôÂçñÂÆ∂ -->
    <view class="message-seller">
      <view class="message-title">ÂèëÊ∂àÊÅØÁªôÂçñÂÆ∂</view>
      <textarea 
        class="message-input" 
        placeholder="ËØ∑ËæìÂÖ•Ê∂àÊÅØÂÜÖÂÆπ..."
        v-model="messageContent"
        auto-height
      ></textarea>
      <button class="send-button" @click="sendMessage">ÂèëÈÄÅ</button>
    </view>
  </view>
</template>

<script>
export default {
  data() {
    return {
      goods: {
        attention: 0,
        cardGraph: 'Á©∫',
        name: 'Á§∫‰æãÂïÜÂìÅÂêçÁß∞Á§∫‰æãÂïÜÂìÅÂêçÁß∞',
        rarity: 'superBigCup',
        price: 99.9, 
        cardNumber: 'ABC123456',
        sellerId: '101',
        id: null,
        isCollected: false
      },
      allGoodsList: [],
      collectedGoodsList: [],
	  messageContent: ''
    }
  },
  methods: {
    // Ëé∑ÂèñÁî®Êà∑Êî∂ËóèÂàóË°®
    getCollectedCards() {
      const cookies = uni.getStorageSync('cookies');
      uni.request({
        url: 'http://127.0.0.1:8080/userFavorite/get',
        method: 'POST',
        header: {
          'Cookie': cookies
        },
        success: (res) => {
          if (res.data.code === 0) {
            this.collectedGoodsList = res.data.data || [];
            // ÈáçÊñ∞Ê£ÄÊü•ÂΩìÂâçÂïÜÂìÅÊòØÂê¶Ë¢´Êî∂Ëóè
            if (this.goods.id) {
              this.checkCollectionStatus();
            }
          }
        }
      });
    },
    
    // Ê£ÄÊü•ÂΩìÂâçÂïÜÂìÅÁöÑÊî∂ËóèÁä∂ÊÄÅ
    checkCollectionStatus() {
      if (!this.goods.id || !this.collectedGoodsList.length) {
        this.goods.isCollected = false;
        return;
      }
      
      const isCollected = this.collectedGoodsList.some(item => 
        String(item.id) === String(this.goods.id)
      );
      
      this.goods.isCollected = isCollected;
      console.log('Êî∂ËóèÁä∂ÊÄÅÊ£ÄÊü•:', this.goods.id, isCollected);
    },
    
    // Ëé∑ÂèñÊâÄÊúâÂïÜÂìÅÊï∞ÊçÆ
    getAllCards() {
      uni.request({
        url: 'http://127.0.0.1:8080/cards/get', 
        method: 'GET',
        success: (res) => {
          if (res.data.code === 0) {
            this.allGoodsList = res.data.data;  
            if (this.goods.id) {
              this.findGoodsById(this.goods.id);
            }
          }
        }
      });
    },
    
    // Ê†πÊçÆIDÊü•ÊâæÂïÜÂìÅÂπ∂ËµãÂÄº
    findGoodsById(id) {
      if (!id || !this.allGoodsList.length) return;
      
      const targetGoods = this.allGoodsList.find(item => 
        String(item.id) === String(id)
      );
      
      if (targetGoods) {
        this.goods = {
          ...targetGoods,
          isCollected: this.goods.isCollected // ‰øùÊåÅÂéüÊúâÁöÑÊî∂ËóèÁä∂ÊÄÅ
        };
        // ÂÜçÊ¨°Ê£ÄÊü•Êî∂ËóèÁä∂ÊÄÅ
        this.checkCollectionStatus();
      }
    },
    Buy(){
      const cookies = uni.getStorageSync('cookies');
      uni.request({
        url: `http://127.0.0.1:8080/cards/buy`,
        method: 'POST',
        data: { id: this.goods.id },
        header: {
            'Content-Type': 'application/x-www-form-urlencoded',
            'Cookie':  cookies
          },
          success: (res) => {
              if (res.data && res.data.code === 0) {
                uni.showToast({ title: 'Ë¥≠‰π∞ÊàêÂäü', icon: 'success' });
              } else {
                uni.showToast({ 
                  title: (res.data && res.data.message) || 'Ë¥≠‰π∞Â§±Ë¥•', 
                  icon: 'none' 
                });
              }
            }
          });
    },
    
    toggleCollect() {
      const cookies = uni.getStorageSync('cookies');
      if (!this.goods.id) {
        uni.showToast({ title: 'ÂïÜÂìÅID‰∏çÂ≠òÂú®', icon: 'none' });
        return;
      }
      
      if (this.goods.isCollected) {
        // ÂèñÊ∂àÊî∂Ëóè
		console.log("ÂèñÊ∂àÊî∂Ëóè"  )
        uni.request({
          url: `http://127.0.0.1:8080/cards/collect/delete`,
          method: 'POST',
          data: { cardId: this.goods.id },
          header: {
              'Content-Type': 'application/x-www-form-urlencoded',
              'Cookie':  cookies
            },
          success: (res) => {
            if (res.data && res.data.code === 0) {
              this.goods.isCollected = false;
              this.collectedGoodsList = this.collectedGoodsList.filter(
                item => item.id.toString() !== this.goods.id.toString()
              );
              uni.showToast({ title: 'Â∑≤ÂèñÊ∂àÊî∂Ëóè', icon: 'success' });
            } else {
              uni.showToast({ 
                title: (res.data && res.data.message) || 'ÂèñÊ∂àÊî∂ËóèÂ§±Ë¥•', 
                icon: 'none' 
              });
            }
          }
        });
      } else {
        // Ê∑ªÂä†Êî∂Ëóè
		console.log("Ê∑ªÂä†Êî∂Ëóè")
        uni.request({
          url: `http://127.0.0.1:8080/cards/collect/add`,
          method: 'POST',
          data: { id: this.goods.id},
          header: {
              'Content-Type': 'application/x-www-form-urlencoded',
              'Cookie':  cookies
            },
          success: (res) => {
            if (res.data && res.data.code === 0) {
              this.goods.isCollected = true;
              this.collectedGoodsList.push({...this.goods});
              uni.showToast({ title: 'Êî∂ËóèÊàêÂäü', icon: 'success' });
            } else {
              console.log('ÊúçÂä°Âô®ÂìçÂ∫î:', res.data);
              uni.showToast({ 
                title: (res.data && res.data.message) || 'Êî∂ËóèÂ§±Ë¥•', 
                icon: 'none' 
              });
            }
          }
        });
      }
    },
	    sendMessage() {
	      if (!this.messageContent.trim()) {
	        uni.showToast({
	          title: 'Ê∂àÊÅØÂÜÖÂÆπ‰∏çËÉΩ‰∏∫Á©∫',
	          icon: 'none'
	        });
	        return;
	      }
	      
	      const cookies = uni.getStorageSync('cookies');
		  console.log(this.messageContent);
	      uni.request({
	        url: 'http://127.0.0.1:8080/post/send',
	        method: 'POST',
	        data: {
	          sellerId: this.goods.sellerId,
	          message: this.messageContent,
	        },
	        header: {
	          'Content-Type': 'application/x-www-form-urlencoded',
	          'Cookie': cookies
	        },
	        success: (res) => {
	          if (res.data.code === 0) {
	            uni.showToast({
	              title: 'Ê∂àÊÅØÂèëÈÄÅÊàêÂäü',
	              icon: 'success'
	            });
	            this.messageContent = '';
	          } else {
				  console.log(res.data);
	            uni.showToast({
	              title: res.data.message || 'Ê∂àÊÅØÂèëÈÄÅÂ§±Ë¥•',
	              icon: 'none'
	            });
	          }
	        },
	        fail: (err) => {
	          console.error('ÂèëÈÄÅÊ∂àÊÅØÂ§±Ë¥•:', err);
	          uni.showToast({
	            title: 'ÁΩëÁªúÈîôËØØÔºåÂèëÈÄÅÂ§±Ë¥•',
	            icon: 'none'
	          });
	        }
	      });
	    },
  },
  updated() {
      console.log('ÁªÑ‰ª∂Â∑≤Êõ¥Êñ∞ÔºåÂΩìÂâçÂïÜÂìÅÊî∂ËóèÁä∂ÊÄÅ:', this.goods.isCollected);
    },
  onLoad(options) {
      if (options.id) {
        this.goods.id = options.id;
        this.getCollectedCards();
        this.getAllCards();
      }
    }
  }
</script>
